<template>
  <div class="auto">
    <div class="box"  v-if="!hasSells">
      <span >您当前没有历史缴费记录</span>
    </div>
    <div class="box"  v-if="hasSells">
      <div class="box_body" style="z-index: 20; border-bottom:  solid 1px #ccc" v-for="row in historysells">
        <div class="box-row" >
          <span class="box-span">缴费金额：{{parseFloat(row.f_preamount).toFixed(2)}} 元</span>
          <span class="glyphicon glyphicon-time" style="color: #00A9FF;"></span>
          <span style="color: #aaaaaa;">{{row[sell_date].substring(0,10)}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  title: '机表用户列表',
  name: 'historySellList',
  data () {
    return {
      hasSells: false,
      sell_date: ''
    }
  },
  props: ['historysells','selldate'],
  methods: {

  },
  ready () {

  },
  watch : {
    'historysells'(val){
      this.sell_date = this.selldate
      if (val.length > 0) {
        this.hasSells = true
      }
    }
  }
}
</script>
<style>
  .box_body {
    padding: 10px 0 10px 0;
  }
  .box {
    background-color: white;
    padding: 0 17px;
    margin-bottom: 10px;
  }
  .box-row {
    padding: 5px 3px 5px 3px;
    box-sizing: border-box;
  }
  .box-span {
    display: inline-block;
    width: 49%;
    box-sizing: border-box;
    font-size: 14px;
  }
</style>



